// context/StatsContext.js
import React, { createContext, useEffect, useState } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import dayjs from 'dayjs';

export const StatsContext = createContext();

export const StatsProvider = ({ children }) => {
  const [stats, setStats] = useState({
    highScore: 0,
    longestTime: 0,
    totalPoints: 0,
    streak: 0,
    gamesPlayed: 0,
    lastPlayed: null,
  });

  useEffect(() => {
    loadStats();
  }, []);

  const loadStats = async () => {
    const stored = await AsyncStorage.getItem('userStats');
    if (stored) {
      setStats(JSON.parse(stored));
    }
  };

  const saveStats = async (updatedStats) => {
    setStats(updatedStats);
    await AsyncStorage.setItem('userStats', JSON.stringify(updatedStats));
  };

  const updateStatsAfterGame = async ({ score, duration }) => {
    const now = dayjs();
    const last = stats.lastPlayed ? dayjs(stats.lastPlayed) : null;

    const isNewDay = last ? !now.isSame(last, 'day') : true;
    const isConsecutive = last ? now.diff(last, 'day') === 1 : true;

    const newStreak = isNewDay
      ? isConsecutive ? stats.streak + 1 : 1
      : stats.streak;

    const updated = {
      highScore: Math.max(stats.highScore, score),
      longestTime: Math.max(stats.longestTime, duration),
      totalPoints: stats.totalPoints + score,
      streak: newStreak,
      gamesPlayed: stats.gamesPlayed + 1,
      lastPlayed: now.toISOString(),
    };

    await saveStats(updated);
  };

  const resetStats = async () => {
    const empty = {
      highScore: 0,
      longestTime: 0,
      totalPoints: 0,
      streak: 0,
      gamesPlayed: 0,
      lastPlayed: null,
    };
    await saveStats(empty);
  };

  return (
    <StatsContext.Provider value={{ stats, updateStatsAfterGame, resetStats }}>
      {children}
    </StatsContext.Provider>
  );
};
