import React, { createContext, useEffect, useState } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';

export const SettingsContext = createContext();

export const SettingsProvider = ({ children }) => {
  const [soundOn, setSoundOn] = useState(true);
  const [darkMode, setDarkMode] = useState(false);
  const [showHints, setShowHints] = useState(false);

  useEffect(() => {
    const loadSettings = async () => {
      const storedSound = await AsyncStorage.getItem('soundOn');
      const storedDark = await AsyncStorage.getItem('darkMode');
      const storedHints = await AsyncStorage.getItem('showHints');

      if (storedSound !== null) setSoundOn(storedSound === 'true');
      if (storedDark !== null) setDarkMode(storedDark === 'true');
      if (storedHints !== null) setShowHints(storedHints === 'true');
    };
    loadSettings();
  }, []);

  const updateSound = async (value) => {
    setSoundOn(value);
    await AsyncStorage.setItem('soundOn', value.toString());
  };

  const updateDarkMode = async (value) => {
    setDarkMode(value);
    await AsyncStorage.setItem('darkMode', value.toString());
  };

  const updateHints = async (value) => {
    setShowHints(value);
    await AsyncStorage.setItem('showHints', value.toString());
  };

  return (
    <SettingsContext.Provider
      value={{ soundOn, darkMode, showHints, updateSound, updateDarkMode, updateHints }}
    >
      {children}
    </SettingsContext.Provider>
  );
};
