import { LETTER_DATA } from '../constants/letterData';
import { VOWELS } from './vowelUtils';

let cachedWordLists = {};

// ✅ Load word list from public folder using fetch (works on web & native)
export const loadWordList = async (language) => {
  if (cachedWordLists[language]) {
    console.log(`✅ Using cached wordlist for ${language} (${cachedWordLists[language].size} words)`);
    return cachedWordLists[language];
  }

  const key = language.toLowerCase();

  try {
    // For React Native, we need to use require
    const wordListPath = require(`../public/wordlists/${key}.txt`);
    
    // Read the file content
    const response = await fetch(wordListPath);
    if (!response.ok) {
      throw new Error(`Failed to load wordlist: ${response.status}`);
    }
    
    const fileContent = await response.text();
    const words = fileContent.split('\n').map((w) => w.trim().toUpperCase());
    const wordSet = new Set(words);
    
    console.log(`✅ Loaded ${wordSet.size} words for ${language}`);
    console.log(`📝 Sample words:`, Array.from(wordSet).slice(0, 5));
    
    cachedWordLists[language] = wordSet;
    return wordSet;
  } catch (error) {
    console.error(`❌ Failed to load wordlist for ${language}:`, error);
    return new Set();
  }
};

// ✅ Check if a letter is a vowel
const isVowel = (char) => ['A', 'E', 'I', 'O', 'U'].includes(char);

// ✅ Generate a random letter grid with 2+ vowels and 2+ consonants
export const getRandomLetters = (count = 9, language = 'English', requireBalanced = true) => {
  const letterData = LETTER_DATA[language] || LETTER_DATA.English;
  const vowelSet = new Set(VOWELS[language] || VOWELS.English);

  const letterPool = [];

  Object.entries(letterData).forEach(([char, { frequency }]) => {
    for (let i = 0; i < frequency; i++) {
      letterPool.push(char);
    }
  });

  const generate = () => {
    const letters = [];
    while (letters.length < count) {
      const char = letterPool[Math.floor(Math.random() * letterPool.length)];
      letters.push(char);
    }
    return letters;
  };

  const isBalanced = (letters) => {
    const vowels = letters.filter((l) => vowelSet.has(l));
    const consonants = letters.filter((l) => !vowelSet.has(l));
    return vowels.length >= 2 && consonants.length >= 2;
  };

  // If balance isn't required (e.g. tradeIn < 4), skip checking
  if (!requireBalanced || count < 4) {
    return generate();
  }

  let attempt = 0;
  while (attempt < 10) {
    const result = generate();
    if (isBalanced(result)) return result;
    attempt++;
  }

  // Fallback after 10 tries
  return generate();
};


// ✅ Check if a word is valid
export const isValidWord = (word, wordListSet) => {
  return wordListSet.has(word.toUpperCase());
};
