import React, { useContext, useEffect } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { GameContext } from '../context/GameContext';

const TimerDisplay = () => {
  const { gameTime, freezeTimeLeft, timerFrozen, timerRunning } = useContext(GameContext);

  const formatTime = (seconds) => {
    if (seconds === undefined || seconds === null) return '00:00';
    const min = String(Math.floor(seconds / 60)).padStart(2, '0');
    const sec = String(seconds % 60).padStart(2, '0');
    return `${min}:${sec}`;
  };

  // Enhanced debugging
  useEffect(() => {
    console.log('⏱️ TimerDisplay render →', { 
      gameTime, 
      freezeTimeLeft, 
      timerFrozen,
      timerRunning,
      displayValue: timerFrozen ? gameTime : gameTime
    });
  }, [gameTime, freezeTimeLeft, timerFrozen, timerRunning]);

  // Always show the game time in the main timer, even during freeze
  return (
    <View style={styles.container}>
      <Text style={styles.timer}>
        {formatTime(gameTime)}
      </Text>
      {timerFrozen && freezeTimeLeft > 0 && (
        <Text style={styles.freeze}>Freeze: {formatTime(freezeTimeLeft)}</Text>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    alignItems: 'flex-end',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    padding: 5,
    borderRadius: 5,
  },
  timer: {
    fontSize: 18,
    fontWeight: 'bold',
    color: '#FFFFFF',
    textAlign: 'right',
  },
  freeze: {
    fontSize: 14,
    color: '#00BFFF',
    fontWeight: '500',
    marginTop: 2,
  },
});

export default TimerDisplay;
