import React, { useContext, useState } from 'react';
import {
  View,
  Text,
  TextInput,
  Button,
  StyleSheet,
  Alert,
  KeyboardAvoidingView,
  Platform,
  TouchableOpacity,
} from 'react-native';
import { AuthContext } from '../context/AuthContext';
import { useTheme } from '../context/ThemeContext';

const LoginScreen = ({ navigation }) => {
  const { login, setUser } = useContext(AuthContext);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const theme = useTheme();

  const handleLogin = async () => {
    if (!email.trim() || !password.trim()) {
      Alert.alert('Missing Info', 'Please enter your email and password.');
      return;
    }

    try {
      await login(email.trim(), password.trim());
      navigation.replace('Game');
    } catch (error) {
      Alert.alert('Login Failed', error.message || 'Incorrect email or password.');
    }
  };

  const handleGuestPlay = () => {
    setUser({ username: 'Guest', email: null });
    navigation.replace('Game'); // GameScreen will start the game
  };

  return (
    <KeyboardAvoidingView
      style={[styles.container, { backgroundColor: theme.background }]}
      behavior={Platform.OS === 'ios' ? 'padding' : 'height'}
    >
      <Text style={[styles.title, { color: theme.text }]}>Word Game</Text>
      
      <View style={styles.form}>
        <TextInput
          style={[styles.input, { 
            backgroundColor: theme.card,
            color: theme.text,
            borderColor: theme.border
          }]}
          placeholder="Email"
          placeholderTextColor={theme.border}
          value={email}
          onChangeText={setEmail}
          autoCapitalize="none"
          keyboardType="email-address"
        />
        
        <TextInput
          style={[styles.input, { 
            backgroundColor: theme.card,
            color: theme.text,
            borderColor: theme.border
          }]}
          placeholder="Password"
          placeholderTextColor={theme.border}
          value={password}
          onChangeText={setPassword}
          secureTextEntry
        />
        
        <Button title="Login" onPress={handleLogin} />
        <Button title="Play as Guest" onPress={handleGuestPlay} />
      </View>
      
      <View style={styles.footer}>
        <TouchableOpacity onPress={() => navigation.navigate('Register')}>
          <Text style={[styles.link, { color: theme.highlight }]}>
            Don't have an account? Register
          </Text>
        </TouchableOpacity>
        
        <TouchableOpacity 
          style={styles.settingsButton} 
          onPress={() => navigation.navigate('Settings')}
        >
          <Text style={[styles.link, { color: theme.highlight }]}>
            Settings
          </Text>
        </TouchableOpacity>
      </View>
    </KeyboardAvoidingView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 24,
    justifyContent: 'center',
  },
  title: {
    fontSize: 36,
    fontWeight: 'bold',
    textAlign: 'center',
    marginBottom: 48,
  },
  form: {
    gap: 16,
  },
  input: {
    height: 50,
    borderWidth: 1,
    borderRadius: 8,
    paddingHorizontal: 16,
    fontSize: 16,
  },
  footer: {
    marginTop: 32,
    alignItems: 'center',
  },
  link: {
    fontSize: 16,
    textDecorationLine: 'underline',
  },
  settingsButton: {
    marginTop: 16,
  },
});

export default LoginScreen;
