import React, { useContext, useEffect, useState } from 'react';
import { View, Text, Button, StyleSheet, Alert, TouchableOpacity } from 'react-native';
import { GameContext } from '../context/GameContext';
import { StatsContext } from '../context/StatsContext';
import { useTheme } from '../context/ThemeContext';

const GameOverScreen = ({ navigation }) => {
  const { score, gameStartTime, resetGame, startGame } = useContext(GameContext);
  const { updateStatsAfterGame } = useContext(StatsContext);
  const theme = useTheme();

  const [duration, setDuration] = useState(0);

  useEffect(() => {
    if (gameStartTime) {
      const now = Date.now();
      const elapsed = Math.floor((now - gameStartTime) / 1000);
      setDuration(elapsed);
      updateStatsAfterGame({ score, duration: elapsed });
    }
  }, [gameStartTime]);

  const getFormattedTime = () => {
    const minutes = String(Math.floor(duration / 60)).padStart(2, '0');
    const seconds = String(duration % 60).padStart(2, '0');
    return `${minutes}:${seconds}`;
  };

  const handlePlayAgain = () => {
    resetGame();
    startGame();
    navigation.navigate('Game');
  };

  const handleBackToMenu = () => {
    resetGame();
    navigation.navigate('Login');
  };

  const handleShare = (platform) => {
    Alert.alert('Coming Soon', `Sharing to ${platform} is coming soon! 🎉`);
  };

  return (
    <View style={[styles.container, { backgroundColor: theme.background }]}>
      <Text style={[styles.title, { color: theme.text }]}>Game Over</Text>
      <Text style={[styles.score, { color: theme.text }]}>Final Score: {score}</Text>
      <Text style={[styles.time, { color: theme.border }]}>Total Time: {getFormattedTime()}</Text>

      <View style={styles.shareSection}>
        <Text style={[styles.shareTitle, { color: theme.text }]}>Share My Score</Text>
        <View style={styles.shareButtons}>
          <TouchableOpacity onPress={() => handleShare('Facebook')}>
            <Text style={[styles.shareButton, { color: theme.highlight }]}>Facebook</Text>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => handleShare('Instagram')}>
            <Text style={[styles.shareButton, { color: theme.highlight }]}>Instagram</Text>
          </TouchableOpacity>
          <TouchableOpacity onPress={() => handleShare('X')}>
            <Text style={[styles.shareButton, { color: theme.highlight }]}>X (Twitter)</Text>
          </TouchableOpacity>
        </View>
      </View>

      <View style={styles.buttonContainer}>
        <Button title="Play Again" onPress={handlePlayAgain} />
        <Button title="Back to Menu" onPress={handleBackToMenu} />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 24,
    justifyContent: 'center',
    alignItems: 'center',
  },
  title: {
    fontSize: 36,
    marginBottom: 20,
  },
  score: {
    fontSize: 24,
    marginBottom: 10,
  },
  time: {
    fontSize: 20,
    marginBottom: 30,
  },
  buttonContainer: {
    gap: 12,
    width: '100%',
    paddingHorizontal: 20,
    marginTop: 24,
  },
  shareSection: {
    marginBottom: 20,
    alignItems: 'center',
  },
  shareTitle: {
    fontSize: 20,
    marginBottom: 8,
  },
  shareButtons: {
    flexDirection: 'row',
    gap: 12,
  },
  shareButton: {
    fontSize: 16,
    textDecorationLine: 'underline',
  },
});

export default GameOverScreen;
