import React, { useContext, useEffect, useState } from 'react';
import { View, StyleSheet, Alert } from 'react-native';
import HeaderBar from '../components/HeaderBar';
import WordInputDisplay from '../components/WordInputDisplay';
import ActionButtons from '../components/ActionButtons';
import LetterGrid from '../components/LetterGrid';
import SubmitButton from '../components/SubmitButton';
import { GameContext } from '../context/GameContext';
import { LanguageContext } from '../context/LanguageContext';
import { loadWordList, isValidWord, getRandomLetters } from '../utils/wordUtils';
import { useNavigation } from '@react-navigation/native';
import { useTheme } from '../context/ThemeContext';
import { WORD_MULTIPLIERS } from '../constants/multipliers';

const SCRABBLE_POINTS = {
  A: 1, B: 3, C: 3, D: 2, E: 1, F: 4, G: 2,
  H: 4, I: 1, J: 8, K: 5, L: 1, M: 3, N: 1,
  O: 1, P: 3, Q: 10, R: 1, S: 1, T: 1, U: 1,
  V: 4, W: 4, X: 8, Y: 4, Z: 10,
};

const calculateScore = (word) => {
  const baseScore = word.toUpperCase().split('').reduce(
    (acc, char) => acc + (SCRABBLE_POINTS[char] || 0),
    0
  );
  const multiplier = WORD_MULTIPLIERS[word.length] || 1.0;
  return Math.round(baseScore * multiplier);
};

const getFreezeDuration = (length, phase) => {
  if (phase === 1) {
    if (length === 4) return 3;
    if (length === 5) return 5;
    if (length >= 6) return 7;
  } else if (phase === 2) {
    if (length === 4) return 1;
    if (length === 5) return 3;
    if (length >= 6) return 5;
  } else if (phase === 3) {
    if (length === 5) return 1;
    if (length >= 6) return 3;
  }
  return 0;
};

const GameScreen = () => {
  const {
    currentWord,
    setCurrentWord,
    score,
    setScore,
    gameTime,
    selectedIndices,
    setSelectedIndices,
    setGridLetters,
    startGame,
    backspace,
    tradeIn,
    shuffleLetters,
    gamePhase,
    triggerFreeze,
    setAlertWithTimeout,
  } = useContext(GameContext);

  const { language } = useContext(LanguageContext);
  const navigation = useNavigation();
  const theme = useTheme();

  const [wordList, setWordList] = useState(new Set());

  useEffect(() => {
    const fetchWords = async () => {
      const list = await loadWordList(language);
      console.log(`✅ Loaded ${list.size} words for ${language}`);
      setWordList(list);
    };
    fetchWords();
  }, [language]);

  useEffect(() => {
    startGame();
  }, []);

  useEffect(() => {
    if (gameTime <= 0 && gameTime !== null) {
      navigation.navigate('GameOver');
    }
  }, [gameTime]);

  const handleSubmit = () => {
    const word = currentWord.join('');
    if (word.length < 3) {
      setAlertWithTimeout('Words must be at least 3 letters!');
      return;
    }

    console.log(`🔍 Validating word: "${word}"`);
    console.log(`📚 Word list size: ${wordList.size}`);
    console.log(`🔍 Word in list: ${wordList.has(word.toUpperCase())}`);

    if (isValidWord(word, wordList)) {
      const earned = calculateScore(word);
      setScore((prev) => prev + earned);
      
      // Show points earned message
      setAlertWithTimeout(`"${word}" earned ${earned} points!`);
      
      const freezeSeconds = getFreezeDuration(word.length, gamePhase);
      if (freezeSeconds > 0) {
        // Trigger freeze immediately
        triggerFreeze(freezeSeconds);
        // Show freeze message after a short delay to not overlap with points message
        setTimeout(() => {
          setAlertWithTimeout(`+${freezeSeconds} second freeze!`);
        }, 1000);
      }

      setGridLetters((prevLetters) => {
        const newLetters = [...prevLetters];
        const replacements = getRandomLetters(selectedIndices.length, language);
        selectedIndices.forEach((index, i) => {
          newLetters[index] = replacements[i];
        });
        return newLetters;
      });

      setCurrentWord([]);
      setSelectedIndices([]);
    } else {
      setAlertWithTimeout(`"${word}" is not a valid word!`);
      setCurrentWord([]);
      setSelectedIndices([]);
    }
  };

  return (
    <View style={[styles.container, { backgroundColor: theme.background }]}>
      <HeaderBar />
      <WordInputDisplay />
      <ActionButtons
        onBackspace={backspace}
        onTradeIn={() => tradeIn(language, false)}
        onShuffle={shuffleLetters}
      />
      <LetterGrid />
      <SubmitButton onSubmit={handleSubmit} />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    paddingVertical: 20,
    paddingHorizontal: 12,
    justifyContent: 'space-between',
  },
});

export default GameScreen;
