// context/GameContext.js
import React, {
  createContext,
  useEffect,
  useState,
  useRef,
  useContext,
} from 'react';
import { getRandomLetters } from '../utils/wordUtils';
import { LanguageContext } from './LanguageContext';

export const GameContext = createContext();

export const GameProvider = ({ children }) => {
  const { language } = useContext(LanguageContext);

  const [score, setScore] = useState(0);
  const [gameTime, setGameTime] = useState(60);
  const [timerRunning, setTimerRunning] = useState(false);
  const [timerFrozen, setTimerFrozen] = useState(false);
  const [freezeTimeLeft, setFreezeTimeLeft] = useState(0);
  const [alertMessage, setAlertMessage] = useState('');

  const [gridLetters, setGridLetters] = useState([]);
  const [currentWord, setCurrentWord] = useState([]);
  const [selectedIndices, setSelectedIndices] = useState([]);
  const [gameStartTime, setGameStartTime] = useState(null);
  const [gamePhase, setGamePhase] = useState(1);

  const timerRef = useRef(null);
  const freezeRef = useRef(null);
  const alertTimeoutRef = useRef(null);

  // Debug timer state changes
  useEffect(() => {
    console.log('🔄 Timer state changed:', { 
      gameTime, 
      timerRunning, 
      timerFrozen, 
      freezeTimeLeft 
    });
  }, [gameTime, timerRunning, timerFrozen, freezeTimeLeft]);

  const updateGamePhase = (elapsedSeconds) => {
    if (elapsedSeconds >= 360) return 3;
    if (elapsedSeconds >= 180) return 2;
    return 1;
  };

  const generateLetterGrid = () => getRandomLetters(9, language);

  const startGame = () => {
    if (!language) return;
    clearInterval(timerRef.current);
    clearInterval(freezeRef.current);
    if (alertTimeoutRef.current) clearTimeout(alertTimeoutRef.current);
    setScore(0);
    setGameTime(60);
    setCurrentWord([]);
    setSelectedIndices([]);
    setGridLetters(generateLetterGrid());
    setTimerRunning(true);
    setTimerFrozen(false);
    setFreezeTimeLeft(0);
    setGameStartTime(Date.now());
    setGamePhase(1);
    setAlertMessage('');
  };

  const resetGame = () => {
    clearInterval(timerRef.current);
    clearInterval(freezeRef.current);
    if (alertTimeoutRef.current) clearTimeout(alertTimeoutRef.current);
    setScore(0);
    setGameTime(60);
    setTimerRunning(false);
    setCurrentWord([]);
    setSelectedIndices([]);
    setGridLetters(generateLetterGrid());
    setGameStartTime(null);
    setGamePhase(1);
    setTimerFrozen(false);
    setFreezeTimeLeft(0);
    setAlertMessage('');
  };

  const backspace = () => {
    if (currentWord.length === 0 || selectedIndices.length === 0) return;
    setCurrentWord((prev) => prev.slice(0, -1));
    setSelectedIndices((prev) => prev.slice(0, -1));
  };

  const tradeIn = () => {
    if (selectedIndices.length === 0) return;
    const newLetters = [...gridLetters];
    const replacements = getRandomLetters(selectedIndices.length, language);
    selectedIndices.forEach((index, i) => {
      newLetters[index] = replacements[i];
    });
    setGridLetters(newLetters);
    setCurrentWord([]);
    setSelectedIndices([]);
    setScore((prev) => Math.max(prev - 5, 0)); // deduct 5 points, not below 0
    setAlertMessage('Traded in letters! -5 points');
    if (alertTimeoutRef.current) clearTimeout(alertTimeoutRef.current);
    alertTimeoutRef.current = setTimeout(() => {
      setAlertMessage('');
    }, 3000);
  };

  const shuffleLetters = () => {
    const shuffled = [...gridLetters];
    for (let i = shuffled.length - 1; i > 0; i--) {
      const j = Math.floor(Math.random() * (i + 1));
      [shuffled[i], shuffled[j]] = [shuffled[j], shuffled[i]];
    }
    setGridLetters(shuffled);
    setCurrentWord([]);
    setSelectedIndices([]);
    setAlertMessage('Letters shuffled!');
    if (alertTimeoutRef.current) clearTimeout(alertTimeoutRef.current);
    alertTimeoutRef.current = setTimeout(() => {
      setAlertMessage('');
    }, 3000);
  };

  // Function to set alert message with auto-clear
  const setAlertWithTimeout = (message, duration = 3000) => {
    if (alertTimeoutRef.current) clearTimeout(alertTimeoutRef.current);
    setAlertMessage(message);
    alertTimeoutRef.current = setTimeout(() => {
      setAlertMessage('');
    }, duration);
  };

  // Completely revised freeze timer logic
  const triggerFreeze = (duration) => {
    if (duration <= 0) return;
    
    // Clear the main timer
    if (timerRef.current) {
      clearInterval(timerRef.current);
      timerRef.current = null;
    }
    
    // Set freeze state
    setTimerFrozen(true);
    setFreezeTimeLeft(duration);
    
    // Clear any existing freeze timer
    if (freezeRef.current) {
      clearInterval(freezeRef.current);
      freezeRef.current = null;
    }
    
    // Start the freeze countdown
    freezeRef.current = setInterval(() => {
      setFreezeTimeLeft((prev) => {
        if (prev <= 1) {
          // Freeze timer is done
          clearInterval(freezeRef.current);
          freezeRef.current = null;
          setTimerFrozen(false);
          
          // Restart the main timer immediately
          if (timerRunning) {
            if (timerRef.current) {
              clearInterval(timerRef.current);
            }
            
            timerRef.current = setInterval(() => {
              setGameTime((prev) => {
                const next = prev - 1;
                if (next <= 0) {
                  clearInterval(timerRef.current);
                  setTimerRunning(false);
                  return 0;
                }
                return next;
              });
            }, 1000);
          }
          
          return 0;
        }
        return prev - 1;
      });
    }, 1000);
  };

  // Main timer effect - simplified to only handle initial timer start
  useEffect(() => {
    if (timerRunning && !timerFrozen) {
      // Clear any existing timer
      if (timerRef.current) {
        clearInterval(timerRef.current);
        timerRef.current = null;
      }
      
      // Start the main timer
      timerRef.current = setInterval(() => {
        setGameTime((prev) => {
          const next = prev - 1;
          if (next <= 0) {
            clearInterval(timerRef.current);
            setTimerRunning(false);
            return 0;
          }
          return next;
        });
      }, 1000);
    }

    return () => {
      if (timerRef.current) {
        clearInterval(timerRef.current);
        timerRef.current = null;
      }
    };
  }, [timerRunning, timerFrozen]);

  useEffect(() => {
    if (gameStartTime) {
      const interval = setInterval(() => {
        const elapsed = Math.floor((Date.now() - gameStartTime) / 1000);
        setGamePhase(updateGamePhase(elapsed));
      }, 5000);
      return () => clearInterval(interval);
    }
  }, [gameStartTime]);

  return (
    <GameContext.Provider
      value={{
        score,
        setScore,
        gameTime,
        timerRunning,
        timerFrozen,
        freezeTimeLeft,
        gridLetters,
        setGridLetters,
        currentWord,
        setCurrentWord,
        selectedIndices,
        setSelectedIndices,
        gamePhase,
        setTimerRunning,
        startGame,
        resetGame,
        backspace,
        tradeIn,
        shuffleLetters,
        triggerFreeze,
        alertMessage,
        setAlertMessage,
        setAlertWithTimeout,
      }}
    >
      {children}
    </GameContext.Provider>
  );
};
