import React, { useContext, useEffect, useState } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import { GameContext } from '../context/GameContext';
import { SettingsContext } from '../context/SettingsContext';
import { LanguageContext } from '../context/LanguageContext';
import { AuthContext } from '../context/AuthContext';
import { useTheme } from '../context/ThemeContext';
import { loadWordList } from '../utils/wordUtils';
import TimerDisplay from './TimerDisplay';

const HeaderBar = () => {
  const { 
    score, 
    gridLetters, 
    gameTime, 
    timerRunning, 
    timerFrozen,
    alertMessage 
  } = useContext(GameContext);
  const { showHints } = useContext(SettingsContext);
  const { language } = useContext(LanguageContext);
  const { user } = useContext(AuthContext);
  const theme = useTheme();

  const [wordList, setWordList] = useState(new Set());
  const [hint, setHint] = useState('');
  const username = user?.username || 'Guest';

  // Debug timer state in HeaderBar
  useEffect(() => {
    console.log('📊 HeaderBar timer state:', { 
      gameTime, 
      timerRunning, 
      timerFrozen 
    });
  }, [gameTime, timerRunning, timerFrozen]);

  useEffect(() => {
    const fetchList = async () => {
      const words = await loadWordList(language);
      setWordList(words);
    };
    fetchList();
  }, [language]);

  // Generate hint when grid letters change
  useEffect(() => {
    if (!showHints || gridLetters.length === 0 || wordList.size === 0) {
      setHint('');
      return;
    }

    const letters = gridLetters.join('');
    let longest = '';

    wordList.forEach(word => {
      const upperWord = word.toUpperCase();
      if (upperWord.length >= 3 && isWordInGrid(upperWord, letters)) {
        if (upperWord.length > longest.length) {
          longest = upperWord;
        }
      }
    });

    if (longest.length >= 3) {
      const hintString = `${longest[0]}${'-'.repeat(longest.length - 2)}${longest[longest.length - 1]}`;
      setHint(hintString);
    } else {
      setHint('');
    }
  }, [showHints, gridLetters, wordList]);

  const isWordInGrid = (word, letters) => {
    const tempLetters = [...letters];
    for (let char of word) {
      const index = tempLetters.indexOf(char);
      if (index === -1) return false;
      tempLetters.splice(index, 1);
    }
    return true;
  };

  return (
    <View style={styles.container}>
      <Text style={[styles.text, { color: theme.text }]}>
        SCORE{'\n'}{score}
      </Text>

      <View style={styles.center}>
        <Text style={[styles.message, { color: theme.text }]}>
          {alertMessage || (showHints && hint ? hint : '')}
        </Text>
        <Text style={[styles.username, { color: theme.border }]}>👤 {username}</Text>
      </View>

      <View style={styles.timerWrapper}>
        <TimerDisplay />
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    marginBottom: 12,
    paddingHorizontal: 4,
  },
  text: {
    fontSize: 16,
    textAlign: 'center',
  },
  message: {
    fontSize: 18,
    fontWeight: '600',
    textAlign: 'center',
    minHeight: 24, // Ensure consistent height
  },
  username: {
    fontSize: 14,
    marginTop: 4,
    textAlign: 'center',
  },
  center: {
    alignItems: 'center',
  },
  timerWrapper: {
    alignItems: 'flex-end',
    minWidth: 80,
    zIndex: 10,
  },
});

export default HeaderBar;
