import React from 'react';
import { TouchableOpacity, Text, StyleSheet } from 'react-native';
import { useTheme } from '../context/ThemeContext';

const SubmitButton = ({ onSubmit, disabled }) => {
  const theme = useTheme();

  return (
    <TouchableOpacity
      style={[
        styles.button,
        {
          backgroundColor: disabled ? theme.border : theme.highlight,
          opacity: disabled ? 0.6 : 1,
        },
      ]}
      onPress={onSubmit}
      disabled={disabled}
    >
      <Text style={[styles.text, { color: theme.text }]}>SUBMIT</Text>
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  button: {
    paddingVertical: 16,
    borderRadius: 12,
    alignItems: 'center',
    marginTop: 12,
  },
  text: {
    fontSize: 18,
    fontWeight: 'bold',
  },
});

export default SubmitButton;
