// context/AuthContext.js
import React, { createContext, useState, useEffect } from 'react';
import AsyncStorage from '@react-native-async-storage/async-storage';

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  const login = async (email, password) => {
    const savedCredentials = await AsyncStorage.getItem('credentials');
    if (!savedCredentials) throw new Error('No account found');

    const parsed = JSON.parse(savedCredentials);
    if (parsed.email === email && parsed.password === password) {
      const userData = { username: parsed.username, email };
      setUser(userData);
      await AsyncStorage.setItem('user', JSON.stringify(userData));
    } else {
      throw new Error('Invalid email or password');
    }
  };

  const logout = async () => {
    setUser(null);
    await AsyncStorage.removeItem('user');
  };

  const createAccount = async (username, email, password) => {
    const newUser = { username, email, password };
    await AsyncStorage.setItem('credentials', JSON.stringify(newUser));
    await login(email, password);
  };

  const checkLoginStatus = async () => {
    try {
      const storedUser = await AsyncStorage.getItem('user');
      if (storedUser) {
        setUser(JSON.parse(storedUser));
      }
    } catch (e) {
      console.error('Error loading user:', e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    checkLoginStatus();
  }, []);

  return (
    <AuthContext.Provider value={{ user, setUser, loading, login, logout, createAccount }}>
      {children}
    </AuthContext.Provider>
  );
};
