import React, { useContext } from 'react';
import { View, Text, TouchableOpacity, StyleSheet } from 'react-native';
import { GameContext } from '../context/GameContext';
import { useTheme } from '../context/ThemeContext';

// ✅ Add Scrabble points reference
const SCRABBLE_POINTS = {
  A: 1, B: 3, C: 3, D: 2, E: 1, F: 4, G: 2,
  H: 4, I: 1, J: 8, K: 5, L: 1, M: 3, N: 1,
  O: 1, P: 3, Q: 10, R: 1, S: 1, T: 1, U: 1,
  V: 4, W: 4, X: 8, Y: 4, Z: 10,
};

const LetterGrid = () => {
  const {
    gridLetters,
    currentWord,
    setCurrentWord,
    selectedIndices,
    setSelectedIndices,
  } = useContext(GameContext);
  const theme = useTheme();

  const getRows = () => {
    const rows = [];
    for (let i = 0; i < gridLetters.length; i += 3) {
      rows.push(gridLetters.slice(i, i + 3));
    }
    return rows;
  };

  const handleLetterPress = (letter, globalIndex) => {
    if (selectedIndices.includes(globalIndex)) return;
    setCurrentWord([...currentWord, letter]);
    setSelectedIndices([...selectedIndices, globalIndex]);
  };

  return (
    <View style={styles.grid}>
      {getRows().map((row, rowIndex) => (
        <View key={rowIndex} style={styles.row}>
          {row.map((letter, colIndex) => {
            const globalIndex = rowIndex * 3 + colIndex;
            const isSelected = selectedIndices.includes(globalIndex);
            const upperLetter = letter.toUpperCase();
            const points = SCRABBLE_POINTS[upperLetter] || 1;

            return (
              <TouchableOpacity
                key={globalIndex}
                disabled={isSelected}
                onPress={() => handleLetterPress(letter, globalIndex)}
                style={[
                  styles.tile,
                  { backgroundColor: isSelected ? theme.background : theme.card },
                ]}
              >
                <Text style={[styles.letter, { color: theme.text }]}>
                  {isSelected ? '' : letter}
                </Text>
                <Text style={[styles.points, { color: theme.border }]}>
                  {isSelected ? '' : points}
                </Text>
              </TouchableOpacity>
            );
          })}
        </View>
      ))}
    </View>
  );
};

const styles = StyleSheet.create({
  grid: {
    gap: 12,
    marginBottom: 16,
  },
  row: {
    flexDirection: 'row',
    justifyContent: 'center',
    gap: 12,
  },
  tile: {
    width: 80,
    height: 80,
    alignItems: 'center',
    justifyContent: 'center',
    borderRadius: 10,
  },
  letter: {
    fontSize: 28,
    fontWeight: 'bold',
  },
  points: {
    fontSize: 12,
  },
});

export default LetterGrid;
