import React, { useContext } from 'react';
import { View, Text, StyleSheet, Button } from 'react-native';
import { AuthContext } from '../context/AuthContext';
import { StatsContext } from '../context/StatsContext';
import { useTheme } from '../context/ThemeContext';

const StatisticsScreen = ({ navigation }) => {
  const { user } = useContext(AuthContext);
  const { stats } = useContext(StatsContext);
  const theme = useTheme();

  if (!user) {
    return (
      <View style={[styles.container, { backgroundColor: theme.background }]}>
        <Text style={[styles.title, { color: theme.text }]}>Statistics</Text>
        <Text style={[styles.message, { color: theme.text }]}>
          You must be logged in to view statistics.
        </Text>
        <Button title="Back to Menu" onPress={() => navigation.goBack()} />
      </View>
    );
  }

  const formattedTime = `${Math.floor(stats.longestTime / 60)}m ${stats.longestTime % 60}s`;

  return (
    <View style={[styles.container, { backgroundColor: theme.background }]}>
      <Text style={[styles.title, { color: theme.text }]}>Your Statistics</Text>

      <Stat label="High Score" value={stats.highScore} theme={theme} />
      <Stat label="Longest Time Played" value={formattedTime} theme={theme} />
      <Stat label="Total Points Earned" value={stats.totalPoints} theme={theme} />
      <Stat label="Daily Streak" value={`${stats.streak} days`} theme={theme} />
      <Stat label="Games Played" value={stats.gamesPlayed} theme={theme} />

      <View style={{ marginTop: 24 }}>
        <Button title="Back to Menu" onPress={() => navigation.goBack()} />
      </View>
    </View>
  );
};

const Stat = ({ label, value, theme }) => (
  <View style={styles.statRow}>
    <Text style={[styles.statLabel, { color: theme.border }]}>{label}</Text>
    <Text style={[styles.statValue, { color: theme.text }]}>{value}</Text>
  </View>
);

const styles = StyleSheet.create({
  container: {
    flex: 1,
    padding: 24,
  },
  title: {
    fontSize: 28,
    marginBottom: 24,
    textAlign: 'center',
  },
  message: {
    fontSize: 18,
    textAlign: 'center',
    marginBottom: 16,
  },
  statRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 16,
    borderBottomWidth: 1,
    paddingBottom: 6,
  },
  statLabel: {
    fontSize: 16,
  },
  statValue: {
    fontSize: 16,
    fontWeight: 'bold',
  },
});

export default StatisticsScreen;
